import React, {Component} from 'react';
import {Table} from 'semantic-ui-react';
import Item from "./Item";

class Items extends Component {

    render () {
        const items = this.props.items.map((item,index) => {
            return (
              <Item
                key={item.name}
                item={item}
                selected={(item.name === this.props.selectedItem.name)}
                ref={(item.name === this.props.selectedItem.name)?this.props.selectedElementRef:null}
                {...this.props}
              > 
                {item.name}
              </Item>
            )
          })
              
        return (
            <Table inverted unstackable selectable={false} striped={false} fixed width={16} 
                style={{
                // marginBottom:(showAddPopup || showShowMore) ? 20 : '50vh'
                }}
            >
                <Table.Body > 
                    {items}
                </Table.Body>
            </Table>
        )
    }
}

export default Items;




