const color = (tag) => {
    let color="";
    switch (tag) {
        case "Citrus" : {
            color = "#F2711F";
            break;
        }
        case "Greens & Broc" : {
            color = "DarkGreen"
            break; 
        }
        case "Berries & Grapes" : {
            color = "#6538C8"
            break;
        }        
        case "Apples & Pears" : {
            color = "#B3CB1C"
            break;
        }        
        case "Fruiting Veg" : {
            color = "#00B3AC"
            break;
        }        
        // case "Root/Stems" : {
        //     color = "#A236C7"
        //     break;
        // }        
        case "Stone Fruit" : {
            color = "#2884CF"
            break;
        }        
        case "Potatoes & Onions" : {
            color = "#A46741"
            break;
        }        
        case "Tropical & Melons" : {
            color = "#FBBC05"
            break;
        }   
        case "Sprouts" : {
            color = "#DF3C95"
            break;
        }        
        // case "Cruciferous" : {
        //     color = "#23B947"
        //     break;
        // }           
        case "Herbs" : {
//            color = "Green"
            color = "#23B947"
            break;
        }           
        case "Mushrooms" : {
            color = "Tan"
            break;
        }              
        case "Salad" : {
            color = "MediumSeaGreen"
            break;
        }  
        case "Other Veg" : {
            color = "OliveDrab"
            break;
        }  
        case "Misc. Fruit" : {
            color = "Tomato"
            break;
        }  
        case "Roots & Stems" : {
            color = "Peru"
            break;
        }
        case "Tomatoes" : {
            color = "FireBrick"
            break;
        }  
        default : color = "";
    }
    return color;
}

export default color;


