import React, {Component} from 'react';
import {Modal, Form, Button, Message, Container} from 'semantic-ui-react';
import {titleCaseNoTrim} from './Strings';
// import color from '../colors';

// let initialItem = null;
const DELETE_COUNT=5;

class EditItemForm extends Component {

    item = {name:""};

    state = {
        name: this.props.item.name,
        tags: this.props.item.tags,
        notes:this.props.item.notes,
        errorMessage:'',
        deleteCounter:DELETE_COUNT
    }
    
    onOpen = () => {
        // console.log('EditItemForm.onOpen');
        this.props.onOpen();
    }
    onClose = () => {
        // console.log('EditItemForm.onClose');
        this.props.onClose();
    }
    handleChange = (e, { name, value }) => {
        this.setState({ [name]: titleCaseNoTrim(value), errorMessage:'' });
    }
    handleDeleteClick = () => {
        const deleteCount = this.state.deleteCounter - 1;
        if (deleteCount === 0)  {
            this.setState({deleteCounter: DELETE_COUNT});
            this.props.onDelete(this.props.item);
        }
        else
            this.setState({deleteCounter: deleteCount});
    }
    handleSubmit = () => {
        if ((this.props.item.name !== this.state.name) && (this.props.items.filter(item => (item.name === this.state.name)).length > 0)) {
            this.setState({errorMessage:'"' + this.state.name + '" already exists'});
            return ;
        }
        this.props.onSubmit(this.props.item, this.state)
        this.props.onClose();
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.item.name) {
            this.setState({name: nextProps.item.name, tags: nextProps.item.tags, notes:nextProps.item.notes, errorMessage:'', deleteCounter:DELETE_COUNT})
        }
    }
    render() {
        // console.log(this.state.deleteCounter);
        return (
            <Form onSubmit={this.handleSubmit}>
            <Modal basic
                style={{position:'fixed', top:'30%'}}
                open={this.props.open} 
                onOpen={this.onOpen} //doesn't fire!!! Use componentDidUpdate instead.
                onClose={this.onClose}
                closeIcon
                closeOnDimmerClick={false}
            >
                <Modal.Header>
                    Edit Item
                </Modal.Header>
                <p/>
                <p/>
                    <Modal.Content>
                        <Form.Group>
                        <Form.Input autoCapitalize='words' required fluid type="text"
                            name="name"
                            label="Name" 
                            value={this.state.name}
                            onChange={this.handleChange}
                            placeholder="This field is required"
                            // defaultValue={this.props.item.name}
                        />
                        { this.state.errorMessage &&
                            <Message
                                error
                                // header='Action Forbidden'
                                content={this.state.errorMessage}
                            />
                        }
                        <p/>
                        <Form.Dropdown required fluid selection 
                            name="tags"
                            label="Category"  
                            options={Array.from(new Set(this.props.items.map(item => item.tags))).sort().map(tags => {return {value:tags,text:tags}})} 
                            value={this.state.tags}
                            onChange={this.handleChange}
                            placeholder="This field is required"
                            // defaultValue={this.props.item.tags}
                        />
                        <p/>
                        {/* <Form.Input fluid type="text"
                            name="notes"
                            label="Notes"  
                            onChange={this.handleChange}
                            value={this.state.notes}
                            placeholder='Enter notes'
                            // defaultValue={this.props.item.notes}
                        /> */}
                        </Form.Group>
                    </Modal.Content>
                    {/* <Modal.Actions> */}
                        <p/>
                        <Container>
                            {(this.state.deleteCounter === DELETE_COUNT) ?
                                <Button negative onClick={this.handleDeleteClick} floated="left"    >
                                    Delete
                                </Button>
                                :
                                <Button negative onClick={this.handleDeleteClick}>
                                    Deleting in {this.state.deleteCounter} clicks                           
                                </Button>
                            }
                            <Button onClick={this.handleSubmit} floated="right">
                                Submit
                            </Button>
                        </Container>
                    {/* </Modal.Actions> */}
            </Modal>
            </Form>
        )
    }
}

export default EditItemForm;



