import React, { Component } from 'react';
import { Button} from 'semantic-ui-react';
import SidebarView from './SidebarView';
import IndexKey from './IndexKey';


function KeypadRow(props) {
    return (
        <Button.Group widths="3" compact className="black" style={{height:38}}>
            {props.children}
        </Button.Group>
    )
}

class IndexPortal extends Component {

    state = {
        selectedLetter: "",
        searchMode:this.props.searchMode,
        startingLetters: []
    }

    componentWillReceiveProps = (nextProps) => {
        // console.log("IndexPortal.componentWillReceiveProps", nextProps.items.length,this.props.items.length);
        if (nextProps.items.length === this.props.items.length)
            return;
        let startLetters=[];
        nextProps.items.forEach((item) => {
          startLetters.push(item.name.charAt(0).toUpperCase());
        });
        let distinctLetters = Array.from(new Set(startLetters));
        this.setState({startingLetters:distinctLetters});
    }

    shouldComponentUpdate = (nextProps, nextState) => {
        // console.log(nextProps.open !== this.props.open);
        // return (nextProps.open !== this.props.open || this.state.startingLetters.length === 0 );
        return true;
    }

    // onOpenAZ = (e) => {
    //     let startLetters=[];
    //     this.props.items.forEach((item) => {
    //       startLetters.push(item.name.charAt(0).toUpperCase());
    //     });
    //     let distinctLetters = Array.from(new Set(startLetters));
    //     this.setState({startingLetters:distinctLetters});
    // }    
        
    render() {  
        const renderKey = (label) => {
            return (<IndexKey label={label} {...this.props} disabled={!this.state.startingLetters.includes(label) && !this.props.searchMode}/>)
        }
        return (
            <SidebarView {...this.props}>
                <KeypadRow>
                    {renderKey("A")}
                    {renderKey("B")}
                    {renderKey("C")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("D")}
                    {renderKey("E")}
                    {renderKey("F")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("G")}
                    {renderKey("H")}
                    {renderKey("I")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("J")}
                    {renderKey("K")}
                    {renderKey("L")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("M")}
                    {renderKey("N")}
                    {renderKey("O")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("P")}
                    {renderKey("Q")}
                    {renderKey("R")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("S")}
                    {renderKey("T")}
                    {renderKey("U")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("V")}
                    {renderKey("W")}
                    {renderKey("X")}
                </KeypadRow>
                <KeypadRow>
                    {renderKey("Y")}
                    {renderKey("Z")}
                    <Button
                        style={{borderRadius:"0em"}}
                        disabled={!this.props.searchMode}
                        content="⌫"
                        onClick={this.props.onLetterClick.bind(this, "⌫")} 
                    />
                </KeypadRow>
                <Button className="black" onClick={this.props.onLetterClick.bind(this, " ")} disabled={!this.props.searchMode} style={{margin:0,borderRadius:0}}>[SPACE]</Button>
            </SidebarView>
        );
    }
}

export default IndexPortal;



