import React, {Component} from 'react';
import {Table, Segment} from 'semantic-ui-react'
import color from '../colors';

let buttonPressTimer;
let animateTimer;
class Item extends Component {
    state = {animate:false, timestamp:0}

    handleTouchStart = (e) => {
        buttonPressTimer = setTimeout(() => this.doTimeout() , 3000);
    }
    doTimeout = () => {
        //this.props.deleteItem(this.props.item);
        this.props.onLongPress(this.props.item);
    }
    handleTouchEnd = () => {
        clearTimeout(buttonPressTimer);
    }       
    componentDidUpdate = (prevProps, prevState) => {
        if (this.props.selected) {
            // console.log(this.props);
            // console.log(prevState);
            if (this.props.item.timestamp !== this.state.timestamp) {
                if (this.state.timestamp > 0) 
                    this.animate();
                this.setState({timestamp:this.props.item.timestamp})
            }
        }
    }
    animate = () => {
        clearTimeout(animateTimer);
        this.setState({animate:true});
        if (this.props.item.qty.length < 2)
            animateTimer=setTimeout(this.onAnimationComplete,1500);
        else
            animateTimer=setTimeout(this.onAnimationComplete,500);
    }
    onAnimationComplete = () => {
        // console.log('onAnimationComplete');
        this.setState({animate:false});
        this.props.onInteractionComplete();
    }

    render() {
        const props = this.props;
        const selected=props.selected;
  //      console.log(props.item.timestamp, props.dateCutoff);
        return (
            <Table.Row 
                style={{
                    backgroundColor: selected?(this.state.animate?'#000000':'#FFFFFF'):color(props.item.tags),
                    fontWeight:selected?'bolder':null,
                    opacity:(props.item.timestamp < props.dateCutoff)?0.7:1
                }} 
                onClick={props.onRowClick.bind(this, props.item)} 


                // onRowClick={this.props.onRowClick.bind(this, item, index)}
                // deleteItem={this.props.deleteItem.bind(this,item)}
                // onInteractionComplete={this.props.onInteractionComplete}



                onTouchStart={this.handleTouchStart.bind(this)}
                onTouchEnd={this.handleTouchEnd.bind(this)}
                onTouchMove={this.handleTouchEnd.bind(this)}
            >
                <Table.Cell textAlign="center" width={4}    
                    style={{padding:0, lineHeight:1}}
                >
                    <Segment as="a" basic 
                        style={{
                            padding:0, width:'100%' ,margin:0, color:selected?color(props.item.tags):'white'
                        }}
                    >
                        {props.item.qty} <br/> {props.item.unit}
                    </Segment>
                </Table.Cell>
                <Table.Cell width={12}
                    style={{paddingLeft:5, lineHeight:1, whiteSpace:'preWrap'}}
                > 
                    <Segment as="a" basic style={{margin:0, padding:0, color:selected?color(props.item.tags):'white'}}>
                        {props.item.name} <strong >{props.item.marked ? "*" : ""}</strong>
                        <br/>
                        <i>{props.item.notes}</i>
                    </Segment>
                </Table.Cell>
            </Table.Row>
        )
    }
}

export default Item;




