import React, { PureComponent } from 'react';
import { Button } from 'semantic-ui-react';

let buttonPressTimer;

class IndexKey extends PureComponent {
    handleTouchStart = () =>  {
        // console.log("props.searchMode", this.props.searchMode);
        buttonPressTimer = setTimeout(
            () => this.doTimeout() , 500);
    }
    doTimeout = () => {
        // console.log('doTimeout', this.props.label);
        this.props.setSearchMode();
        this.props.onLetterClick(this.props.label);
    }
    handleTouchEnd = (e) => {
        clearTimeout(buttonPressTimer);
    }    
    render() {
        return (
            <Button 
                style={{borderRadius:"0em", textTransform:this.props.searchMode?'lowercase':'uppercase', opacity:this.props.disabled?0.5:1}}
                // disabled={this.props.disabled}
                onClick={this.props.onLetterClick.bind(this, this.props.label)} 
                onTouchStart={this.handleTouchStart}
                onTouchEnd={this.handleTouchEnd}        
                onTouchMove={this.handleTouchEnd}    
                content={this.props.label}
            />
        )
    }
}

export default IndexKey;


