export function trim(string) {
    var trimmed = string.replace(/^\s+|\s+$/g, '');
    return trimmed;
};
export function ltrim(string) {
    var trimmed = string.replace(/^\s+/g, '');
    return trimmed;
};
export function rtrim(string) {
    var trimmed = string.replace(/\s+$/g, '');
    return trimmed;
};
    
export function titleCase(str) {
    // console.log('titlecase',str.toLowerCase().split(' ').length);
    return trim(str).toLowerCase().split(' ').map(function(word) {
    return (word.length > 0) ? word.replace(word[0], word[0].toUpperCase()) : "";
    }).join(' ');
}

export function titleCaseNoTrim(str) {
    // console.log('titlecase',str.toLowerCase().split(' ').length);
    return str.toLowerCase().split(' ').map(function(word) {
        return (word.length > 0) ? word.replace(word[0], word[0].toUpperCase()) : "";
    }).join(' ');
}
  
 


