import React, { PureComponent } from 'react';
import { List } from 'semantic-ui-react';

class Units extends PureComponent {

  render() {
    
    const unit = (label) => <List.Item as='a' style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.onUnitClick.bind(this,label)}>{label}</List.Item>

    return (
        <List size="small" selection celled inverted horizontal style={{margin:0, marginTop:5, marginBottom:15 }}>
          {unit('bags')}
          {unit('boxes')}
          {unit('trays')}
          {unit('tubs')}
          {unit('bin')}
          {unit('shelf')}
          {unit('bunch')}
          {unit('pcs')}
        </List>
    );
  }
}

export default Units;



