import React, { Component } from 'react';
import { Label, Header, Grid } from 'semantic-ui-react';
import color from '../colors';
import SidebarView from './SidebarView';
// import CategoryItems from './CategoryItems'

class CategoryPortal extends Component {

    shouldComponentUpdate = (nextProps) => {
        // console.log(nextProps.open !== this.props.open);
        return (nextProps.open !== this.props.open);
    }

    handleCategoryClick = (e, category) => {
        // console.log('handleCategoryClick',e, e.type);
        e.preventDefault();
        e.stopPropagation();
        this.props.onCategoryClick(category);
    }

    render() {

        const renderCategory = (category, index) => {
            return (
                <Grid.Column key={index}
                        onClick={(el) => this.handleCategoryClick(el,category)}
                        style={{
                            padding:0,
                            paddingTop:5,
                            // backgroundColor:"gray"
                        }}
                    >
                    <Label as="a"
                        style={{
                            backgroundColor:'transparent', 
                            color:color(category),                 
                            borderWidth:0
                        }}
                    >
                        {category}
                    </Label>
                </Grid.Column>
            )
        }   

        const fruitItems = this.props.items.filter(item => (item.type === "Fruit"));
        const fruitCategories = [...new Set(fruitItems.map(item => item.tags))].sort();
        const renderedFruitCategories = fruitCategories.map((category,index) => {
            return renderCategory(category, index);
        });
        const vegItems = this.props.items.filter(item => (item.type === "Veg"));
        const vegCategories = [...new Set(vegItems.map(item => item.tags))].sort();
        const renderedVegCategories = vegCategories.map((category,index) => {
            return renderCategory(category, index);
        });

        return (
            <SidebarView {...this.props}>
                <Header dividing disabled content="FRUIT" inverted size="tiny"
                    // onClick={this.props.fruitSelected.bind(this, 'All Fruit',"*")}categorySelected
                    style={{marginBottom:4, marginTop:0}}
                />
                <Grid columns={2} verticalAlign="middle" style={{margin:0, padding:0}}>                        
                    {renderedFruitCategories}
                </Grid>
                <Header dividing disabled content="VEG" inverted size="tiny"
                    // onClick={this.props.vegSelected.bind(this, 'All Veg',"*")}
                    style={{marginBottom:4, marginTop:12}}
                />
                <Grid columns={2} verticalAlign="middle" style={{margin:0, padding:0}}>                        
                    {renderedVegCategories}
                </Grid>
            </SidebarView>
        );
    }
}

export default CategoryPortal;



