import React, {PureComponent} from 'react';
import {Menu, Header, Button} from 'semantic-ui-react'
import color from '../colors';
class TopBar extends PureComponent {

    statusToColor = (status) => {
        // console.log('statusToColor', status);
        let ret = "black";
        switch (status) {
          case "saving" :
            ret = "black";
            break;
          case "saved" :
            ret = "green";
            break;
          case "error" :
            ret = "red";
            break;
          default :
            ret = "black";
        }
        return ret;
      }

    //   shouldComponentUpdate = (nextProps) => {
    //     //   console.log(nextProps);
    //       return true;
    //   }

    render () {
        // console.log(this.props.status);
        const search = this.props.search;
        const filter = this.props.filter;
        return (        
            <Menu borderless size="massive" widths={16} 
                className="Top ui fixed compact inverted" 
                style={{position:'fixed', top:0, left:0, width:'100%', height:62}}
                onClick={filter.clear}
            >
                <Menu.Item>
                    <Header inverted style={{opacity:search.mode?1:0.5}}>
                        {(search.text.length && search.mode)?search.text:""}
                    </Header> 
                </Menu.Item>
                <Menu.Item position="left" style={{width:72, position:'fixed', top:0, left:0}}>
                    <Button className="black" size="huge"
                       onClick={this.props.onCounterClick}
                    >
                        {this.props.completedCount}
                    </Button>
                </Menu.Item>
                <Menu.Item position="right" style={{width:72, position:'fixed', top:0, right:0}}>
                    { 
                        (!filter.isFiltered() && this.props.changed) &&
                        <Button color={this.statusToColor(this.props.status)} size="huge" icon="save"
                            onClick={this.props.onSave}
                        />
                    }
                    { 
                        (filter.value().length === 0 && search.text.length === 0 && !this.props.changed) &&
                        <Button className="black" size="huge" icon="check"
                            onClick={this.props.onSend}
                        />
                    }          
                    { 
                        (filter.isFilteredBy('category')) &&
                        <Button className="black" size="huge" icon="circle"
                            style={{color:color(filter.value())}}
                            // onClick={filter.clear}
                        />
                    }
                    {
                        (search.text && !search.mode) &&
                        <Button className="black" size="huge" content={search.text}
                            // onClick={filter.clear}
                        />
                    }
                    { 
                        (search.text && search.mode) && 
                        <Button className="black" size="huge" icon="cancel"
                            // onClick={filter.clear}
                        />
                    }
                </Menu.Item>
            </Menu>
        )
    }
}

export default TopBar;


