import React, {Component} from 'react';
import {Popup, Input} from 'semantic-ui-react'
import {boxShadow} from '../styles';

class PopupNotes extends Component {

    state = { isOpen: false }

    handleOpen = (e) => {
      this.setState({ isOpen: true })
      this.props.onAddNoteClick();
      e.stopPropagation();  
    //   this.timeout = setTimeout(() => {
    //     this.setState({ isOpen: false })
    //   }, timeoutLength)
    }
  
    handleClose = () => {
      this.setState({ isOpen: false })
      clearTimeout(this.timeout)
    }

    handleKeyPress = (e) => {
      if (e.key === "Enter") {
        // console.log(this.input.inputRef.value);
        this.props.noteEntered(this.props.item, this.input.inputRef.value);
        this.setState({ isOpen: false });        
      }
    }

    render() {
        return (
                <Popup
                    wide="very"
                    // style={{width:'92%'}}
                    style={{position:'fixed', height:110, width:'80%', top:'15%', left:'10%', zIndex:100000,
                      boxShadow: boxShadow
                    }}                    
                    basic
                    size="large"
                    flowing
                    header={this.props.item.name}
                    trigger={this.props.trigger}
                    on='click'
                    open={this.state.isOpen}
                    onClose={this.handleClose}
                    onOpen={this.handleOpen}
                    position='top left'                    
                    content={
                      <React.Fragment>
                        <p/>
                        <Input ref={(e) => this.input = e}
                          fluid
                          placeholder={"Enter notes"} 
                          defaultValue={this.props.item.notes}
                          onKeyPress={this.handleKeyPress}
                        />
                      </React.Fragment>
                    }
                  />
        )
    }
}

export default PopupNotes;




