import React, { Component } from 'react';
import { Button } from 'semantic-ui-react';
import PopupNotes from './PopupNotes';
import SidebarView from './SidebarView';
import Units from './Units';

const keyStyle =  {
  textAlign:'center',
  paddingLeft:0,
  paddingRight:0,
  margin:0,
  borderRadius:"0em"
}


class Keypad extends Component {

  render() {
    
    const key = (label) => 
      <Button className="ui black button" style={keyStyle} 
        onClick={this.props.numberClicked}
      >{label}</Button>

    function KeyRow(props) {
      return (
        <Button.Group size="big"  widths="3" style={{padding:'0px 5px'}} > 
          {props.children}
        </Button.Group>
      )
    }
    
    return (
      <SidebarView {...this.props}>
        { (this.props.item.notes.length > 0) ?
            <Button disabled={this.props.item.name.length === 0} icon="cancel" className="black" content="Delete note" style={{margin:5, marginTop:0}}
              onClick={this.props.deleteNote}
            />
          : 
          <PopupNotes
            item={this.props.item} 
            trigger={
              <Button disabled={this.props.item.name.length === 0} icon="edit" className="black" content="Add note" style={{margin:5, marginTop:0}}/>
            }
            {...this.props}
          />
        }
        <KeyRow>
          {key("¼")}{key("½")}{key("¾")}
        </KeyRow>
        <KeyRow>
          {key(1)}{key(2)}{key(3)}
        </KeyRow>
        <KeyRow>
          {key(4)}{key(5)}{key(6)}
        </KeyRow>
        <KeyRow>
          {key(7)}{key(8)}{key(9)}
        </KeyRow>
        <KeyRow>
          <Button className="ui black button" icon="asterisk" onClick={this.props.asteriskClicked} style={keyStyle}/>
          {key(0)}
          <Button className="ui black button" icon="delete" onClick={this.props.deleteClicked} style={keyStyle}/>
        </KeyRow>
        <Units onUnitClick={this.props.unitClicked}/>
      </SidebarView>
    );
  }
}

export default Keypad;



