const strCompare = (s1,s2) => {
  const u1 = s1.toUpperCase();
  const u2 = s2.toUpperCase();
  if (u1 < u2)
    return -1;
  if (u1 > u2)
    return 1;
  return 0;
}

window.oncontextmenu = function(event) {
  event.preventDefault();
  event.stopPropagation();
  return false;
};

const byName = (i1, i2) => {
  return strCompare(i1.name, i2.name);
}

const byTag = (i1, i2) => {
  let compare = strCompare(i1.tags, i2.tags)
  if (compare === 0)
    compare = strCompare(i1.name, i2.name)
  return compare;
}

const timeCompare = (t1, t2) => {
  let d1 = t1?t1:0
  let d2 = t2?t2:0
  if (d1 > d2)
    return 1
  if (d1 < d2)
    return -1
  return 0;
}

const byTimestamp = (i1, i2) => {
  return timeCompare(i1.timestamp, i2.timestamp);
}

const byDateDescThenByTimeAsc = (i1, i2) => {
  function compare(a, b) {
      return a > b || -(a < b);
  }
  let d1, d2;
  if (i1.timestamp)
    d1 = i1.timestamp;
  else
    d1 = 0;
  if (i2.timestamp)
    d2 = i2.timestamp;
  else
    d2 = 0;

  var aISO = new Date(d1).toISOString(),
      bISO = new Date(d2).toISOString();
  return compare(bISO.slice(0, 10), aISO.slice(0, 10))   // date desc
      || compare(aISO.slice(11, 19), bISO.slice(11, 19)); // time asc
};


const byTagThenByTime = (i1, i2) => { 
  let compare = strCompare(i1.tags, i2.tags)
  if (compare === 0)
    compare = timeCompare(i1.timestamp, i2.timestamp)
  return compare;
}

