import React, { Component } from 'react';
import { Icon, Input, Popup } from 'semantic-ui-react';
import {boxShadow} from '../styles';

class AddPopup extends Component {

    state = {open:false};

    onOpen = () => {
        this.setState({open:true});
    }

    onClose = () => {
        this.setState({open:false});
    }
    
    handleKeyPress = (e) => {
        // console.log(e);
        if (e.key === "Enter") {
            // console.log(this.input.inputRef.value);
            this.setState({open:false});
            this.props.onSubmit(this.input.inputRef.value, this.props.category);
        }
    }

    render() {
        return (
            <Popup
                open={this.state.open}
                onOpen={this.onOpen}
                onClose={this.onClose}
                wide="very"
                // style={{width:'92%'}}
                style={{position:'fixed', height:110, width:'80%', top:'15%', left:'10%', zIndex:100000, boxShadow:boxShadow}}                    
                basic
                size="large"
                flowing
                // hideOnScroll
                header={"Add item to " + this.props.category}
                trigger={
                    <Icon inverted size="big" name="add" 
                        style={{ opacity:0.5
                        //              position:'fixed', left:'15%', top:'50%', zIndex:100000,
                            // boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
                        }}
                        // onClick={this.addItem.bind(this, this.filterValue())}
                />
                }
                on='click'
                // open={this.state.isOpen}
                // onClose={this.handleClose}
                // onOpen={this.handleOpen}
                position='top left'                    
                content={
                <React.Fragment>
                    <p/>
                    <Input 
                        ref={(e) => this.input = e}
                        fluid
                        placeholder={"Enter new item name"} 
                        onKeyPress={this.handleKeyPress}
                    />
                </React.Fragment>
                }
            />
        )
    }
}
export default AddPopup;


